import { Component, OnInit } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { ActivatedRoute } from '@angular/router';
import { Message } from "primeng/primeng";
import { CurrentDepositApproveBean } from './bean/currentDepositApprove.bean';
@Component({
    selector: 'currentDepositApprove',
    templateUrl: './currentDepositApprove.component.html',
    styleUrls: ['./currentDepositApprove.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class CurrentDepositApproveComponent implements OnInit {
    code: any;
    productTypeOptions: any ;
    applyId: string;
    depositAccount: string;
    custNo: string;
    custName: string;
    productType: string;
    depositAmount: string;
    depositRate: string;
    largeCustomerFees: string;
    msgs: Message[] = [];
    ch: any = CALENDAR_CN;
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    depositApproveBean: CurrentDepositApproveBean = new CurrentDepositApproveBean();
    constructor(
        public httpService: CusOperationHttpService,
        public routeInfo: ActivatedRoute,
        public commfunc: Commfunc
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
    }

    ngOnInit() {
        this.codeValues();
        this.productTypeOptions = this.code['ProductTypeCurrent'];
        let param = {
            userId: this.userId,
            orgId: this.orgId,
            applyId: this.routeInfo.snapshot.params['applyId'],
        }
        this.httpService.agencyQueryInterestRate(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.applyId = data.applyId;
                this.depositAccount = data.depositAccount;
                this.custNo = data.custNo;
                this.custName = data.custName;
                this.productTypeOptions.forEach(item => {
                    if (item.value == data.productType) {
                        this.productType = item.label;
                    }
                });
                this.depositRate = data.depositRate;
                this.largeCustomerFees = data.largeCustomerFees;
                this.depositAmount = data.depositAmount;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}
